import { render, html } from "lighterhtml";
import { StrictMode } from "react";

// Importing and parse JSON-LD Subject Object.
var json = JSON.parse($('script[type="application/ld+json"]').text());

//Mount First Contentful Paint with lighterhtml
view();
function view() {
  const name = document.querySelector("#schema-CreativeWork-name");
  render(name, () => html.for(name)`<h1>${json.name}</h1>`);
  const image = document.querySelector("#schema-CreativeWork-image");
  render(
    image,
    () => html.for(image)`<img src="${json.image[1]}" alt="${json.name}">`
  );
  const description = document.querySelector(
    "#schema-CreativeWork-description"
  );
  render(description, () => html.for(description)`${json.description}`);
}

//import React, { useState, useEffect } from "react";
//import { Session as AuthSession } from "@inrupt/solid-client-authn-browser";
import ReactDOM from "react-dom";
import App from "./App";

const rootElement = document.getElementById("root");
ReactDOM.render(
  <StrictMode>
    <App />
  </StrictMode>,
  rootElement
);

// Log the JSON
// console.log(json);

// JQuery
const cont = document.getElementById("container");

// Print the name.
var heading = document.createElement("H1");
heading.innerHTML = json.name;

document
  .getElementById("load-schema-CreativeWork-name")
  .addEventListener("click", function () {
    cont.appendChild(heading);
  });

// Print the image.
var image = document.createElement("img");
image.setAttribute("src", json.image[1]);

document
  .getElementById("load-schema-CreativeWork-image")
  .addEventListener("click", function () {
    cont.appendChild(image);
  });

// Print the description.
var description = document.createElement("p");
description.innerHTML = json.description;

document
  .getElementById("load-schema-CreativeWork-description")
  .addEventListener("click", function () {
    cont.appendChild(description);
  });
